import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { ToastProvider } from '../../../providers/toast/toast';
import { ExamTypesProvider } from '../../../providers/exam-types/exam-types';

/**
 * Generated class for the ExamDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-exam-detail',
  templateUrl: 'exam-detail.html',
})
export class ExamDetailPage {

  public item: any = {id: '', code: '', name: ''};

  constructor(public navCtrl: NavController, public navParams: NavParams, private toastPrv: ToastProvider,
              public viewCtrl : ViewController, private examPrv: ExamTypesProvider) {
    this.item = this.navParams.get('item') ?  this.navParams.get('item') : this.item;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ExamDetailPage');
  }

  save() {
    // TODO: validar e salvar especialidade
    if (!this.isValid()) {
      return;
    }

    let payload = this.item;

    this.examPrv.save(payload).subscribe((res) => {
      this.toastPrv.show('Exame salvo com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      console.log(err);
      this.toastPrv.show('Tivemos problemas em salvar o item!');
    });
  }


  isValid(): boolean {

    if (!this.item.code) {
      this.toastPrv.show('Informe o cÃ³digo');
      return false;
    }

    if (!this.item.name) {
      this.toastPrv.show('Informe o nome');
      return false;
    }

    return true;
  }

  remove() {
    this.examPrv.remove(this.item).subscribe((res) => {
      this.toastPrv.show('Item removido com sucesso!');
      this.viewCtrl.dismiss({reload: true});
    }, (err) => {
      console.log(err);
      this.toastPrv.show('Tivemos problemas em remover o item!');
    });
  }

  dismiss() {
    this.viewCtrl.dismiss({reload: false});
  }

}



